export default {
  name: 'homebtn',
  props:{
    isInResult:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return {

    }
  },
  computed:{

  },
  methods:{
    backHomeHandle(){
      event.preventDefault();
      if(this.isInResult){
        window.location.href="../SelectPage/index.html"
      }else{
        window.history.replaceState("","","");
        window.localStorage.removeItem('selectedObj');
        this.$router.push({'path':'/'})
      }

    }
  },
  components:{

  },
  mounted(){

  }
}



